<template id="a">
    <div class="cont" :class="classon?'animated slideInRight':''">
    	<!-- <img src="http://mo.kugou.com/download/static/images/v2/p2.jpg" @click="click()"> -->
    	<div class="div_box" :style="'{transform:translate(0,'+Y+')}'" @click="addY">
    		<div class="div1">
	       		<div class="box"> router{{msg}}</div>
	       		<router-link to="/" class="routers">首页</router-link>
	       		<ul>
	       			<li v-for="item in gridData" :id="item.id" class="animated">
		       			{{item.name}}
		       			<dl v-for="item2 in item.host_list" style="text-align: center;">
		       				<dt><img :src="item2.thumb" alt="" style="width: 50%"></dt>
		       				<dd>{{item2.title}}</dd>
		       			</dl>
		       		</li>
	       		</ul>
	       </div>

	       <div class="div2">
	       		<div class="box"> router{{msg}}</div>
	       		<router-link to="/" class="routers">首页2</router-link>

	       </div>

    	</div>
       
       
    </div>
</template>
<script>

export default {
  name: 'one',
  data () {
    return {
      msg: 'Welcome toone',
      classon:false,
      Y:0,
      gridData: [],
      avc:false,
      apiUrl: 'https://socket.dahangu.cn/index.php/api/live/pindao_list'
    }
  },
  created:function() {
  	this.classon = true
  },
  ready: function() {
    
  },
  methods:{
  	click:function() {
  		console.log('Good job!')
  	},
  	addY:function() {
  		console.log(1)
  		this.Y+=667;
  		// console.log(this.Y)
  		this.getCustomers()
  	},
  	getCustomers: function() {
  		var vm = this;
  		if (vm.gridData.length) {
  			//alert(11)
  			return;
  		}
	    vm.$http.post(vm.apiUrl,{

    headers: {'Content-Type': 'application/json'}
  })
	        .then((response) => {
	            vm.gridData = response.data;
	            console.log(vm.gridData)
	            vm.avc = true;
	            setTimeout(function() {
	            	var list = document.getElementsByTagName('li');
		            for (var i = 0; i < list.length; i++) {
					    (function(a) {
			                setTimeout(function() {
			                    addClassn(list[a],'fadeInUp');
			                    list[a].style.opacity = 1;
			                }, a*100);
			            })(i)
					}
	            },300)
	           
	        })
	        .catch(function(response) {
	            console.log(response)
	        })
	}
  }
}

function addClassn(obj, cls){
    var obj_class = obj.className,
        blank = (obj_class != '') ? ' ' : '',
   		added = obj_class + blank + cls;
    obj.className = added;
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.box{
		font-style: 0.4rem;
		line-height: 0.8rem;
	}
	.cont{
		/*width: 100%;
		height: 100%;*/
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		margin: auto;
		background: url(http://mo.kugou.com/download/static/images/v2/p2.jpg) no-repeat;
		background-size: 100%;
	}
	.routers{
		font: 1.0rem "华文行楷";
	}
	.div1,.div2{
		width: 100%;
		height: 667px;
		background: url(http://mo.kugou.com/download/static/images/v2/p3.jpg) no-repeat;
		background-size: 100%;
	}
	.div2{
		background: url(http://mo.kugou.com/download/static/images/v2/p4.jpg) no-repeat;
	}
	.div_box{
		width: 100%;
		height: 667px;
		/*transform:translate(0px,0px);*/
		transition: .3s;
		overflow-y: hidden;
	}
	li{
		font: .5rem "华文楷体";
		opacity: 0;
	}
	.listyle{
		transform: scale(1.1);
		transition: .5s;
	}
</style>


